.square{
    background: #fff;
    border: 1px solid #999;
    font-size: 60px;
    font-weight: bold;
    width: 80px;
    height: 80px;
    padding:0;
    text-align: center;
    margin-top: -1px;
    margin-left:-1px;
    font-family: 'Play',sans-serif;
    float:left;
    border-radius: 5px;
    box-shadow: 1px 1px 5px 3px #060668;

}