.game {
  width:50%;
  margin: auto;
  padding: 60px 0 30px 0;
  background-color: #3242f2;
}

.game-board{
  display: flex;
  flex-direction: row;
  /* height: 100vh; */
  justify-content: center;
  align-items: center;
}

.game-info{
  margin-top:20px;
  width:100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.m-button {
  width: 200px;
  font-size: 20px;
  font-family:'Play', sans-serif;
  background: #fe4f91;
  color:#322;
  border: 1px solid #fff;
  padding: 3px;
  margin: 5px;
  border-radius: 10px;
  box-shadow: 1px 1px 5px 2px #f20b0b;
  
}

ol{
  padding-inline-start: 0;
}
